<template>
    <div class="qiong-avatar-wrapper">
        <div v-if="src">
            <img :src="src"/>
        </div>
        <div class="qiong-image-blue">
            &nbsp;
        </div>
    </div>
</template>

<script>
import LoadingSmall from '../../Data/LoadingSmall.vue'
    export default {
  components: { LoadingSmall },
        props: {
            src: {
                type: String
            },

        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
.qiong-avatar-wrapper
    width: calc( 10px + 4.6vw )
    height: calc( 10px + 4.6vw )
    border-radius: calc( 1px + 1vw )
    overflow: hidden
    border: 1px solid white !important
    box-sizing: border-box !important

.qiong-avatar-wrapper *
    width: 100% !important
    height: 100% !important
</style>


