<template>
    <div class="qiong-td qiong-txt-td pr-0">
        <div class="qiong-wide-10 qiong-txt-18">
            <!--
            <div class="qiong-txt-16 primary-T-header d-inline">#</div>
            <div class="d-inline primary-L-header">{{ index + 1 }}</div>
            -->
        </div>
        <div class="qiong-wide-12">
            <qiong-avatar-color :src="conf.baseURL + buy.product.avatar"></qiong-avatar-color>
        </div>
        <div class="qiong-wide-50 qiong-pt-10">
            <div class="qiong-txt-22 qiong-txt-primary qiong-txt-shadow">
                {{ buy.product.named }}
            </div>
            <div class="qiong-pt-5 deep-header mt-1">
                <span>數量：{{ buy.num }}</span>
                <span class="sub-header">，&nbsp;</span>
                <span>Pv：{{ buy.product.price }}</span>
            </div>
        </div>
        <div class="qiong-wide-28 text-right qiong-pt-10 pr-6 sub-header qiong-txt-td">
            {{ view.ser_timed(buy.buy_time, true, true) }}購
        </div>
    </div>
</template>

<script>
import QiongAvatarColor from '../../../../components/Qiong/Ui/QiongAvatarColor.vue'
    export default {
        props: {
            index: {
                type: Number
            },
            buy: {
                type: Object
            }
        },
        components: { QiongAvatarColor },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


