<template>
    <div class="qiong-td qiong-pt-15 qiong-pb-10 qiong-zebra-L">
        <div class="qiong-wide-10"></div>
        <div class="qiong-wide-63">
            <div>
                訂單編號：
                <span>
                    {{ view.if_def(buy.order_code) }}
                </span>
            </div>
            <div class="qiong-td">

                <div class="qiong-wide-40">
                    付款人：{{ view.if_def(buy.Origin_buyer_number_code) }}
                </div>
                <div class="qiong-wide-60">
                    Role：{{ view.if_def(buy.role) }}
                </div>
            </div>
            <div class="qiong-td">

                <div class="qiong-wide-40">
                    收貨人：{{ view.if_def(buy.buyer_named) }}&nbsp;{{ view.if_def(buy.buyer_phoned, buy.buyer_email) }}
                </div>
                <div class="qiong-wide-60">
                    收貨地址：{{ view.if_def(buy.buyer_addr) }}
                </div>
            </div>
        </div>
        <div class="qiong-wide-27 text-right pr-12">
            <div>
                &nbsp;
            </div>
            <div class="qiong-td">
                <div class="qiong-wide-40 pr-0">支付方式：</div>
                <div class="qiong-wide-60 pr-0">{{ view.backend.view_pay_way(buy.pay_way) }}</div>
            </div>
            <div class="qiong-td">
                <div class="qiong-wide-40 pr-0">支付金額：</div>
                <div class="qiong-wide-60 pr-0">HK$&nbsp;{{ view.floatMul(buy.num, buy.product.price) }}</div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            index: {
                type: Number
            },
            buy: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


