<template>
    <div></div>
</template>

<script>
    export default {
        name: '',
        data() {
            return {
                buyed: false
            }
        },
        methods: {
            
            async buyLoading(is_me = true) {
                let condition = {
                    'chronu': this.$store.state.chronus.id,
                    '_sort': 'published_at:DESC',
                }
                if (is_me) {    
                    condition['users_permissions_user'] = this.$store.state.user_backend.id
                }

                const res = await this.conn.get(this.api.buys, this.$store.state.token, condition)

                if (res) {
                    if (res.length > 0) {

                        this.buyed = true
                    } else {
                        this.buyed = false
                    }
                }

                return res
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


