<template>
    <v-container fluid class="qiong-txt-td">
        <qiong-row-element class="qiong-pt-10">         
            <v-col class="qiong-txt-24 px-0">
                {{ $store.state.user_backend.display_name }}
            </v-col>
        </qiong-row-element>

        <qiong-row-element>

            <v-col cols="7" class="pl-0">
                <div class=" rounded qiong-user-panel">
                    <qiong-inner-header-element class="deep-header">
                        合資格活躍下線
                    </qiong-inner-header-element>
                    <qiong-space :space="'2vh'"></qiong-space>
                    <uc-active-under-line v-if="$store.state.user_backend" class="qiong-pt-10"></uc-active-under-line>
                </div>
            </v-col>
            <v-col cols="5">
                <div class="h-full w-full rounded qiong-user-panel">
                    <uc-number-msg :me="mine" v-if="mine"></uc-number-msg>  
                </div>   
            </v-col>

            <v-col cols="12" class="pl-0 pt-6">
                <div class=" rounded qiong-user-panel">
                    <qiong-inner-header-element class="deep-header">
                        購買記錄
                    </qiong-inner-header-element>
                    <qiong-space :space="'2vh'"></qiong-space>
                    <uc-buy-record ref="buyRecord" v-if="$store.state.user_backend" class="qiong-pt-10"></uc-buy-record>
                </div>
            </v-col>
        </qiong-row-element>

        <mine ref="mine"></mine>
    </v-container>
</template>

<script>
import Mine from '../../../components/Data/Using/Mine.vue'
import QiongInnerHeaderElement from '../../../components/Qiong/Ele/QiongInnerHeaderElement.vue'
import QiongRowElement from "../../../components/Qiong/Ele/QiongRowElement.vue"
import QiongHeader from '../../../components/Qiong/Pager/QiongHeader.vue'
import QiongPagePanel from '../../../components/Qiong/QiongPagePanel.vue'
import QiongSpace from '../../../components/Qiong/Ui/QiongSpace.vue'
import AlertBuyNone from '../../../components/Viewing/Alert/AlertBuyNone.vue'
import UcActiveUnderLine from './Inner/UcActiveUnderLine.vue'
import UcBuyRecord from './Inner/UcBuyRecord.vue'
import UcNumberMsg from './Inner/UcNumberMsg.vue'

    export default {
        components: { QiongRowElement, QiongPagePanel, UcActiveUnderLine,
                UcBuyRecord,
                QiongInnerHeaderElement,
                QiongSpace,
                AlertBuyNone,
                UcNumberMsg,
                Mine
        },
        name: '',
        data() {
            return {
                mine: null
            }
        },
        mounted() {
            this.mine = this.$refs.mine.lockMine()
        },
    }
</script>

<style lang="sass" scoped>

</style>


