<template>
    <div>

    </div>
</template>

<script>
    export default {
        name: '',
        data() {
            return {
                
            }
        },
        methods: {
            lockMine(model = 'SPONSER') {
                let many, res
                const _this = this
                if (model == 'SPONSER') {
                    many = this.$store.state.users_sponser
                } else {
                    many = this.$store.state.users_enroller
                }
    
                const transfer = this.$store.state.transfer
                
                if (many) {
                    this.bonus._build_res(many, transfer, function(user, transfer) {
                        if (user.unid == _this.$store.state.user_backend.unid) {

                            res = JSON.parse(JSON.stringify(user))
                        }
                    })
                } 
                
                return res
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


