<template>
    <div>
        <div class="qiong-tr qiong-txt-tr">
            <div class="qiong-wide-30">
                <div class=" qiong-pl-10">用戶名稱</div>
            </div>
            <div class="qiong-wide-20">用戶編號</div>
            <div class="qiong-wide-15">iPV</div>
            <div class="qiong-wide-25">支付級別</div>
            <div class="qiong-wide-10"></div>
        </div>
        <qiong-space :space="'1vh'"></qiong-space>
        <div v-if="!loading">
            <div v-if="childrens.length > 0">
                <div class="qiong-td qiong-txt-td qiong-line-hight-20"
                    v-for="(v, i) in childrens" :key="i"

                    :class="{ 'qiong-td-zebra': i % 2 == 0, 'qiong-td-light': i % 2 != 0 }"
                >
                    <div class="qiong-wide-30 qiong-pl-10">{{ v.username }}</div>
                    <div class="qiong-wide-20">{{ v.number_code }}</div>
                    <div class="qiong-wide-15">{{ v.ipv }}</div>
                    <div class="qiong-wide-25">
                        <level-viewing :item="v.vip.named + '_' + v.vip.star"></level-viewing>
                    </div>
                    <div class="qiong-wide-10">
                        <qiong-icon-inset :color="'primary'" :arg="v" @done_Father="openCenter"></qiong-icon-inset>
                    </div>
                </div>
            </div>
            <qiong-empty :bigger="0" v-else></qiong-empty>
        </div>
        <qiong-loading v-else></qiong-loading>

        <qiong-space :space="'3vh'"></qiong-space>
    </div>
</template>

<script>
import QiongIconInset from '../../../../components/Qiong/Button/QiongIconInset.vue'
import QiongEmpty from '../../../../components/Qiong/Ui/QiongEmpty.vue'
import QiongLoading from '../../../../components/Qiong/Ui/QiongLoading.vue'
import QiongSpace from '../../../../components/Qiong/Ui/QiongSpace.vue'
import LevelViewing from '../../../../components/Viewing/LevelViewing.vue'
    export default {
        components: {
            LevelViewing,
                QiongIconInset,
                QiongSpace,
                QiongLoading,
                QiongEmpty
        },
        name: '',
        data() {
            return {
                loading: true,
                childrens: [ ]
            }
        },
        mounted() {
            this.switchChildren()
        },
        methods: {
            switchChildren() {
                this.loading = true
                const _this = this
                let me = this.$store.state.users_enroller[0]
                let many = this.$store.state.users_enroller

                if (many) {
                    this.childrens = [ ]
                    this.bonus._build_res(many, null, function(u, t) {

                        if (u.unid != me.unid && (u.index - me.index) < 2) {
                            if (u.ipv >= _this.bonus.conf.USER_PV_LIMIT) {
                                _this.childrens.push(u)
                            }
                        }
                    })

                    setTimeout(() => {
                        this.loading = false
                    }, 600)
                }
            },

            openCenter(u) {

            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


