<template>
    <div>
        <div v-if="!loading">
            <div v-if="buying.length > 0">
                <div 
                    class="clearfix mb-1-px"
                    v-for="(v, i) in buying" :key="i"
                >
                    <v-expansion-panels accordion flat class="pa-0 qiong-ex-panel">
                        <v-expansion-panel
                        :class="{ 'qiong-zebra-L': i % 2 != 0 }"
                        >
                            <v-expansion-panel-header>
                                
                                <uc-order-item-header :index="i" :buy="v"></uc-order-item-header>
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                
                                <uc-order-item-content :index="i" :buy="v"></uc-order-item-content>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                    </v-expansion-panels>
                </div>
            </div>

            <qiong-empty v-else :bigger="0"></qiong-empty>
        </div>
        <qiong-loading v-else></qiong-loading>

        <qiong-space :space="'3vh'"></qiong-space>
        
        <buy-me ref="buyMe"></buy-me>
    </div>
</template>

<script>
import BuyMe from '../../../../components/Data/Using/BuyMe.vue'
import QiongEmpty from '../../../../components/Qiong/Ui/QiongEmpty.vue'
import QiongLoading from '../../../../components/Qiong/Ui/QiongLoading.vue'
import QiongSpace from '../../../../components/Qiong/Ui/QiongSpace.vue' 
import UcOrderItemContent from '../Order/UcOrderItemContent.vue'
import UcOrderItemHeader from '../Order/UcOrderItemHeader.vue'

    export default {
        components: { 
            BuyMe,
            QiongSpace,
            QiongEmpty,
            QiongLoading,
                UcOrderItemContent,
                UcOrderItemHeader
        },
        name: '',
        data() {
            return {
                buying: [ ],
                loading: true
            }
        },
        mounted() {
            this.dataLoading()
        },
        methods: {

            async dataLoading() {
                
                this.loading = true
                this.buying = await this.$refs.buyMe.buyLoading()

                // this.buying.push(...this.buying)
                setTimeout(() => {
                    this.loading = false
                }, 700)
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


